import React from 'react';
import Nav from '../../Components/Nav/Nav';
import './ProfileScreen.css';
import avatar from '../../assets/avatar.png';
import { useSelector } from 'react-redux';
import { selectUser } from '../../features/userSlice';
import { auth } from '../../firebase/firebase';

const ProfileScreen = () => {
  const user = useSelector(selectUser);

  return (
    <div className="profileScreen">
      <Nav />

      <div className="proflieScreen__body">
        <h1>Edit Profile</h1>
        <div className="profileScreen__info">
          <img src={avatar} alt="" />
          <div className="profileScreen__details">
            <h2>{user.email}</h2>
            <div className="profileScreen__plans">
              <h3>Plans</h3>
              <p>Reneval date 2021/12/03</p>
              <div className="profileScreen__detail">
                <h4>
                  Netflix Standart <br /> <span>1080p</span>
                </h4>
                <button>Subscribe</button>
              </div>
              <div className="profileScreen__detail">
                <h4>
                  Netflix Basic <br /> <span>480p</span>
                </h4>
                <button>Subscribe</button>
              </div>
              <div className="profileScreen__detail">
                <h4>
                  Netflix Premium <br /> <span>4K+HDR</span>
                </h4>
                <button className="current">Current Package</button>
              </div>
              <button
                onClick={() => auth.signOut()}
                className="profileScreen__signOut"
              >
                Sign Out
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProfileScreen;
