import React, { useEffect, useState } from 'react';
import './Row.css';
import axios from '../../api/axios';

const Row = ({ title, isLargeRow = false, fetchUrl }) => {
  const [movies, setMovies] = useState([]);
  const [openPlayer, setOpenPlayer] = useState(false);
  const [movieId, setMovieId] = useState();
  const [youtubeKey, setYoutubeKey] = useState('');

  // https://api.themoviedb.org/3/movie/484718/videos?api_key=742fe5bffcc367117cb0b623259272a9 base url

  const base_url = 'https://image.tmdb.org/t/p/original/';
  const base_url_trailer = 'https://api.themoviedb.org/3/movie/';

  useEffect(() => {
    const fetchData = async () => {
      const request = await axios.get(fetchUrl);
      setMovies(request.data.results);
      return request;
    };

    fetchData();
  }, []);

  const openPlayerHander = (id) => {
    setOpenPlayer((prev) => !prev);
    fetch(
      `https://api.themoviedb.org/3/movie/${id}/videos?api_key=742fe5bffcc367117cb0b623259272a9`
    )
      .then((res) => res.json())
      .then((data) => {
        data && data.results.length && data.results.length > 0
          ? setYoutubeKey(data.results[0].key)
          : setYoutubeKey('Hs-1_HNALhw');
      });
  };

  console.log(openPlayer);

  return (
    <div className="row">
      <h2>{title}</h2>

      <div className="row__posters">
        {movies &&
          movies?.map((movie) => (
            <img
              className={`row__poster ${isLargeRow && 'row__posterLarge'}`}
              onClick={() => openPlayerHander(movie.id)}
              key={movie?.id}
              src={`${base_url}${
                movie && movie.poster_path && movie.backdrop_path && isLargeRow
                  ? movie?.poster_path
                  : movie?.backdrop_path
              }`}
              alt={movie?.name}
            />
          ))}
      </div>
      {openPlayer && (
        <div className="row__videoContainer">
          <iframe
            className="row__video"
            width="560"
            height="315"
            src={`https://www.youtube-nocookie.com/embed/${youtubeKey}?controls=0`}
            frameborder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowfullscreen
          ></iframe>
        </div>
      )}

      {console.log(
        `${base_url_trailer}${movieId}?api_key=742fe5bffcc367117cb0b623259272a9`
      )}
    </div>
  );
};

export default Row;
